/* montserrat-regular - cyrillic */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Montserrat Regular"), local("Montserrat-Regular"), url("../fonts/montserrat-v14-cyrillic-regular.woff2") format("woff2"), url("../fonts/montserrat-v14-cyrillic-regular.woff") format("woff"); }

/* montserrat-500 - cyrillic */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Montserrat Medium"), local("Montserrat-Medium"), url("../fonts/montserrat-v14-cyrillic-500.woff2") format("woff2"), url("../fonts/montserrat-v14-cyrillic-500.woff") format("woff"); }

/* montserrat-600 - cyrillic */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Montserrat SemiBold"), local("Montserrat-SemiBold"), url("../fonts/montserrat-v14-cyrillic-600.woff2") format("woff2"), url("../fonts/montserrat-v14-cyrillic-600.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.wrapper {
  max-width: 1160px;
  margin: 0 auto; }

.page-header {
  position: relative;
  background-color: #003d78; }
  .page-header--footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: #011c40; }

.page-header__wrapper {
  padding: 35px 0;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .page-header__wrapper--footer {
    padding: 46px 0;
    justify-content: flex-start; }

.page-header__logo {
  width: 70px;
  height: 38px;
  fill: #f9fbfd; }
  .page-header__logo--footer {
    margin-right: 42px; }

.page-header__nav {
  width: 51%; }

.page-header__toggle {
  display: none; }

.page-header__menu {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  .page-header__menu--footer {
    width: 51%;
    margin-right: auto; }

.page-header__link {
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  color: #f9fbfd; }

@media (max-width: 1023px) {
  .page-header__wrapper {
    padding: 30px 45px 0; }
    .page-header__wrapper--footer {
      padding: 48px 45px; }
  .page-header__logo {
    width: 38px;
    height: 20px; }
    .page-header__logo--footer {
      margin-right: 25px; }
  .page-header__nav {
    width: 74%; }
  .page-header__menu--footer {
    width: 65%; }
  .page-header__link {
    font-size: 14px;
    line-height: 17px; }
    .page-header__link--footer {
      font-size: 12px;
      line-height: 15px; } }

@media (max-width: 767px) {
  .page-header {
    background-color: #f9fbfd; }
    .page-header--footer {
      background-color: #011c40; }
  .page-header__wrapper {
    min-height: 300px;
    padding: 20px 15px 52px;
    flex-direction: column;
    align-items: flex-start; }
    .page-header__wrapper--footer {
      min-height: 283px;
      padding: 40px 15px 35px; }
  .page-header__logo {
    width: 42px;
    height: 22px;
    fill: #011c40; }
    .page-header__logo--footer {
      margin-bottom: 23px;
      fill: #f9fbfd; }
  .page-header__toggle {
    position: absolute;
    top: 22px;
    right: 11px;
    z-index: 2;
    display: block;
    width: 22px;
    height: 19px;
    background-color: transparent;
    cursor: pointer;
    border: none; }
  .page-header--closed .page-header__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 25px;
    height: 3px;
    background-color: #011c40;
    box-shadow: 0 8px 0 0 #011c40, 0 16px 0 0 #011c40; }
  .page-header--opened .page-header__toggle::before, .page-header--opened .page-header__toggle::after {
    position: absolute;
    top: 9px;
    right: -2px;
    width: 25px;
    height: 2px;
    background-color: #011c40;
    content: ""; }
  .page-header--opened .page-header__toggle::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .page-header--opened .page-header__toggle::after {
    transform: rotate(-45deg); }
  .page-header__menu {
    flex-direction: column; }
  .page-header__link {
    font-size: 18px;
    line-height: 22px;
    padding: 10px 0;
    display: block;
    color: #011c40; }
    .page-header__link--footer {
      font-size: 12px;
      line-height: 15px;
      margin-bottom: 17px;
      padding: 0;
      color: #f9fbfd; }
  .page-header--closed .page-header__menu {
    display: none; }
  .page-header--nojs .page-header__toggle {
    display: none; }
  .page-header--nojs .page-header__link {
    display: block; } }

.social__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex; }

.social__item:not(:last-child) {
  margin-right: 33px; }

@media (max-width: 1023px) {
  .social__list {
    padding-top: 4px; }
  .social__item:not(:last-child) {
    margin-right: 23px; }
  .social__icon--fb {
    width: 9px;
    height: 16px; }
  .social__icon--insta {
    width: 16px;
    height: 16px; }
  .social__icon--twiter {
    width: 16px;
    height: 13px; } }

@media (max-width: 767px) {
  .social__list {
    padding-top: 10px; } }
